@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:ital,wght@1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
.nav-bar {
  background-color: #c5a2b4;
}

.title-text {
  font-family: "Merriweather Sans", sans-serif;
  font-size: 5rem;
  text-align: center;
  padding: 1rem;
}

.home-page-container {
  background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.1),
      rgba(0, 0, 0, 0.3)
    ),
    url(/react-portfolio/static/media/hero.f0eba8f0.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
}

.title-container {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.subtitle {
  color: rgb(90, 90, 90);
  text-align: center;
}

.about-me-page-container {
  text-align: center;
}

.tech-icon {
  margin: 1rem;
}

.subheading {
  margin: 2rem;
}

.form-label {
  margin-top: 1rem;
}

.contact-me-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.contact-form-container {
  flex-basis: 40%;
}

.portfolio-card {
  max-width: 60%;
  background-color: #c9adcf9d;
}

.card-title {
  font-family: "Playfair Display", serif;
  text-align: center;
  font-size: 2rem;
}

* {
  font-family: "Roboto Mono", monospace;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  background-color: #cdacbbb0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

